<template>
  <div
    v-for="(dep, idx) in department"
    :key="dep"
    class="col-12 p-0 d-flex"
    @click="() => clickDepartment(idx + 1)"
  >
    <div class="m-2 p-2 shadow-sm bg-light rounded d-flex flex-fill flex-column align-self-stretch">
      <div class="mx-2 d-lg-flex flex-row">
        <h5
          :class="['m-0 align-self-center flex-fill', { 'click': idx < department.length - 1 }]"
          v-text="dep"
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "PbContactDepartment",
  props: {
    department: {
      type: Array,
      default: [],
    }
  },
  emits: ['click'],
  methods: {
    clickDepartment(size) {
      if (size < this.department.length) {
        this.$emit('click', this.department.slice(0, size).join(' '))
      }
    }
  }
}
</script>

<style scoped>
.click {
  cursor: pointer;
}
</style>